<template>
  <div class="black-bg">
    <div class="white-bg">
      <h4>{{ room.title }}</h4>
      <p>{{ room.content }}</p>
      <img :src="room.image" alt="room0" class="roomImage" />
      <p>{{ month }} Í∞úÏõî ÏÑ†ÌÉù :{{ room.price * month }}Ïõê</p>
      <p class="discount">Ìï†Ïù∏: {{ discount }} %</p>
      (1Ï¥àÏóê 1%Ïî© Í∞êÏÜåÌï©ÎãàÎã§)
      <p class="input-box"><input v-model="month" /> Í∞úÏõî ÏûÑÎåÄ</p>
      <button @click="$emit('closeModal')" class="closeButton">Îã´Í∏∞</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Modal",

  data() {
    return {
      month: 3,
      discount: 30,
    };
  },

  watch: {
    month(num) {
      if (num > 13) {
        alert("13Í∞úÏõîÎ≥¥Îã§ Îçî Î™ªÏÇº");
        this.month = 1;
      } else if (isNaN(num)) {
        alert("Ïà´ÏûêÎ°ú ÏûÖÎ†•ÌïòÏÖà");
        this.month = 1;
      }
    },
  },

  props: {
    room: Object,
  },

  created() {
    let timer = setInterval(() => {
      this.discount--;
    }, 1000);

    setTimeout(() => {
      clearInterval(timer);
    }, 30000);
  },
};
</script>

